import React from "react";

import { Button, Typography } from "@mui/material";

import PictureAsPdfIcon from "@mui/icons-material/PictureAsPdf";
import { red } from "@mui/material/colors";

interface CVContentProps{

};

interface CVContentState{
    cvPath: string,
    cvImagePath: string
};

class CVContent extends React.Component<CVContentProps,CVContentState>{
    constructor(props:CVContentProps){
        super(props);
        this.state={
            cvPath:"/pdf/CV.pdf",
            cvImagePath:"/img/cv.png"
        }
    }

    render(){
        const state= this.state;
        return(
            <div id = "CVArticle" style={{
                alignItems: 'center',
                flexWrap: 'wrap',
                width: "80",
                marginTop: "auto",
                position: "fixed",
                top: "12.5%",
                left: "30%",
                margin: 0,
                padding:"0px 20px 60px 0",
                overflow: "auto"
            }}>
                <Typography variant="h5">
                    Curriculum Vitae
                </Typography>
                Ci dessous se trouve mon Curriculum vitae. Vous pouvez le télécharger au format PDF ou enregistrer l'image.
                <br/>
                <br/>
                <a
                    href={state.cvImagePath}
                >
                    <img
                        src={state.cvImagePath}
                        style={{
                            display:"block",
                            height:512,
                            width:400,
                            marginLeft:"auto",
                            marginRight:"auto"
                        }}
                        alt="cv"
                    />
                </a>
                <br/>
                <br/>
                <Button
                    variant="outlined"
                    startIcon={<PictureAsPdfIcon
                        style={{color:red[500]}}
                        />}
                    href={state.cvPath}
                    download
                    style={{
                        color:"#000000",
                        backgroundColor:"#ffffff",
                        borderColor: "#000000"
                    }}
                >
                    Télécharger le CV
                </Button>
            </div>
        )
    }
}

export default CVContent;